* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', "Arial", sans-serif;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
}
.container {
  max-width: 1100px;
  margin: 0 auto;
}
.body {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr min-content;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
  position: relative;
}
.body--index {
  display: flex;
  flex-direction: column;
}
.body--menu-open {
  overflow: hidden;
}
.body--menu-open::before {
  content: '';
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
}
.body--modal-open {
  overflow: hidden;
}
.body--modal-open::before {
  content: '';
  z-index: 3;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
}
.header {
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 2;
  background-color: #f7f7f7;
}
.header--no-bg {
  background-color: transparent;
}
.header--sticky {
  position: sticky;
}
.header__wrap {
  position: relative;
  display: grid;
  grid-template-columns: max-content 1fr;
  align-items: center;
  padding: 20px;
}
@media (max-width: 900px) {
  .header__wrap {
    grid-template-columns: 1fr min-content;
    padding: 0 20px;
  }
}
.header__title {
  z-index: 1;
  font-family: 'Open Sans', "Arial", sans-serif;
  font-size: 20px;
  font-weight: 400;
  color: #1a1c22;
  text-transform: uppercase;
  transition: all 0.3s ease;
}
.header__title--index-page {
  color: #ffffff;
}
.header__title--menu-open {
  color: #1a1c22;
  transition: all 0.3s ease;
}
.header__menu-button {
  width: 80px;
  height: 80px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  display: none;
}
@media (max-width: 900px) {
  .header__menu-button {
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.nav {
  display: flex;
  justify-content: end;
  align-items: center;
}
@media (max-width: 900px) {
  .nav {
    position: absolute;
    width: 100%;
    top: -200%;
    transition: all 0.5s ease-in;
  }
}
.nav--open {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
  transition: all 0.5s ease;
  justify-content: center;
  background-color: #f7f7f7;
}
.nav--open .nav__list {
  padding: 80px 20px;
  pointer-events: all;
  opacity: 1;
  transition: all 0.5s ease;
}
.nav--open .nav__link {
  color: #1a1c22;
}
.nav--open .nav__link::after {
  background-color: #1a1c22;
}
.nav__list {
  display: flex;
  justify-content: end;
  align-items: center;
  gap: 20px;
  list-style: none;
}
@media (max-width: 900px) {
  .nav__list {
    flex-direction: column;
    justify-content: start;
    align-items: center;
    width: 100%;
    opacity: 0;
    pointer-events: none;
    transition: all 0.5s ease;
  }
}
@media (max-width: 900px) {
  .nav__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
  }
}
.nav__link {
  padding: 8px 0;
  position: relative;
  font-family: 'Open Sans', "Arial", sans-serif;
  color: #1a1c22;
  font-weight: 500;
  font-size: 18px;
  text-decoration: none;
  text-transform: capitalize;
  user-select: none;
  opacity: 0.7;
  transition: 0.2s all ease-in;
}
.nav__link::after {
  display: none;
  position: absolute;
  content: '';
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 1px;
  background-color: #1a1c22;
  transition: 0.2s all ease-in;
  border-radius: 20%;
}
.nav__link:hover,
.nav__link:focus {
  opacity: 1;
  transition: 0.2s all ease-in;
}
.nav__link:hover::after,
.nav__link:focus::after {
  display: block;
}
.nav__link--index-page {
  color: #ffffff;
}
.nav__link--index-page::after {
  background-color: #ffffff;
}
.nav__link--current {
  opacity: 1;
  pointer-events: none;
}
.nav__link--current::after {
  display: block;
}
@media (max-width: 900px) {
  .nav__link {
    width: 100%;
    font-size: 22px;
    text-align: center;
  }
}
.ham {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.hamRotate.active {
  transform: rotate(45deg);
}
.line {
  fill: none;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  stroke: #000;
  stroke-width: 5.5;
  stroke-linecap: round;
}
.ham7 .top {
  stroke-dasharray: 40 82;
}
.ham7 .middle {
  stroke-dasharray: 40 111;
}
.ham7 .bottom {
  stroke-dasharray: 40 161;
}
.ham7.active .top {
  stroke-dasharray: 17 82;
  stroke-dashoffset: -62px;
}
.ham7.active .middle {
  stroke-dashoffset: 23px;
}
.ham7.active .bottom {
  stroke-dashoffset: -83px;
}
