.social-list {
  list-style: none;
  display: flex;
  gap: 10px;
  align-items: center;
}
.social-list__item {
  position: relative;
  --borderWidth: 2px;
  border-radius: 50%;
}
.social-list__item:active::before {
  content: '';
  position: absolute;
  top: calc(-1 * var(--borderWidth));
  left: calc(-1 * var(--borderWidth));
  height: calc(100% + var(--borderWidth) * 2);
  width: calc(100% + var(--borderWidth) * 2);
  background: linear-gradient(60deg, #f79533, #f37055, #ef4e7b, #a166ab, #5073b8, #1098ad, #07b39b, #6fba82);
  border-radius: 50%;
  animation: animatedgradient 3s ease alternate infinite;
  background-size: 300% 300%;
}
.social-list__link {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.15s ease;
}
.social-list__link:hover,
.social-list__link:focus {
  color: #ffffff;
}
.social-list__link:hover .social-list__social-name,
.social-list__link:focus .social-list__social-name {
  visibility: visible;
  opacity: 1;
  transform: translate(-50%, -150%);
}
.social-list__link--instagram {
  background: #527fa6;
  color: #ffffff;
}
.social-list__link--instagram .social-list__social-name {
  background: #527fa6;
  color: currentColor;
}
.social-list__link--instagram .social-list__social-name:after {
  border-top-color: #527fa6;
}
.social-list__link--facebook {
  background: #3b5a9b;
  color: #ffffff;
}
.social-list__link--facebook .social-list__social-name {
  background: #3b5a9b;
  color: currentColor;
}
.social-list__link--facebook .social-list__social-name:after {
  border-top-color: #3b5a9b;
}
.social-list__link--email {
  background: #000;
  color: #ffffff;
}
.social-list__link--email .social-list__social-name {
  background: #000;
  color: currentColor;
}
.social-list__link--email .social-list__social-name:after {
  border-top-color: #000;
}
.social-list__social-name {
  display: block;
  position: absolute;
  top: 0;
  left: 50%;
  padding: 10px 12px;
  border-radius: 40px;
  opacity: 0;
  pointer-events: none;
  text-transform: uppercase;
  transform: translate(-50%, -100%);
  transition: all 0.3s ease;
  z-index: 1;
  font-family: 'Open Sans', "Arial", sans-serif;
  font-size: 12px;
  font-weight: 400;
  white-space: nowrap;
}
.social-list__social-name:after {
  content: "";
  position: absolute;
  display: block;
  bottom: 1px;
  left: 50%;
  width: 0;
  height: 0;
  border: solid;
  border-width: 10px 10px 0 10px;
  border-color: transparent;
  transform: translate(-50%, 100%);
}
.social-list__icon {
  width: 25px;
  height: 25px;
  fill: #ffffff;
}
