.project {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: min-content 1fr;
  grid-row-gap: 40px;

  padding: 0 0 20px 0;
}

.project__gallery {
  min-height: 600px;
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

.project__info {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 30px;
  padding: 0 @basic-side-padding;
}

.project__info-title {
  font-family: @basic-font;
  font-size: @proj-info-title-font-size;
  color: @standart-before-hover-text-color;
  font-weight: @proj-info-title-font-weight;
  text-transform: capitalize;
}

.project__project-dexcription {
  font-family: @basic-font;
  font-size: @proj-desc-font-size;
  font-weight: @proj-desc-font-weight;
  color: @standart-text-color;
}
